:root {
  --background: #f5f5f5;
  --foreground: #171717;



  --navbackground10: #ffffff10;
  --navbackground25: #ffffff25;
  --navbackground50: #ffffff50;
  --navbackground75: #ffffff75;
  --navbackground90: #ffffff90;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #333333;
    --foreground: #ededed;

    --navbackground10: #00000010;
    --navbackground25: #00000025;
    --navbackground50: #00000050;
    --navbackground75: #00000075;
    --navbackground90: #00000090;
  }
}

@font-face {  
